<template>
  <div id="LevelConfirmation">
    <h1>
      Level management
    </h1>
    <v-input-colorpicker v-model="color" />
    {{color}}
  </div>
</template>

<script>
export default {
  name: "LevelManagement",
  data(){
    return{
      color: '#000000'
    }
  },
  computed: {
    game_levels(){
      return this.$store.getters.game_levels;
    },
    completed_game_levels(){
      return this.$store.getters.completed_game_levels;
    },
    user_levels() {
      return this.$store.getters.user_levels;
    },
    completed_user_levels() {
      return this.$store.getters.completed_user_levels;
    },
  }
}
</script>

