<template>
  <div id="login-app">
    <b-container class="login-box login-pos">
      <b-row class="justify-content-md-center" align-h="center">
        <b-col cols="6" class="login">
          <b-row class="my-2">
            <b-col class="col">
              <div class="fit-width mx-auto">
                <img src="../../images/logo_cropped.png" height="30" width="191"/>
              </div>
            </b-col>

          </b-row>

          <b-row class=" my-2">
            <b-col>
              <input id="userinput" class="form-control" placeholder="Username" type="text">
            </b-col>
          </b-row>

          <b-row class="my-2">
            <b-col>
              <input id="passinput" class="form-control" placeholder="Password" type="password">
            </b-col>
          </b-row>

          <b-row class="my-2 justify-content-md-center">
            <b-col>
              <div class="fit-width mx-auto">
                <b-button variant="primary" :style="{background: '#00a2e8'}" squared @click="login()">Login</b-button>
              </div>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "Login",
  data(){
    return{
      game_levels: {},

    }
  },
  methods: {
    login:function (){
      //prihlaseni usera
      this.$store.state.current_user.id = 1;
      this.$store.state.current_user.username = 'xvasat04';
      this.$store.state.current_user.date_of_registration = '2020-06-01';
      this.$store.state.current_user.role = 1;
      this.$store.state.current_user.icon = null;
      this.$store.state.current_user.dark_mode = 1;
      this.$store.state.current_user.music = 1;

      //nastaveni uspesne authentikace
      this.$store.state.isAuthenticated = true;

      this.$cookies.set('current_logged_user', this.$store.state.current_user, '30m');
      this.$cookies.set('isAuthenticated', this.$store.state.isAuthenticated, '30m');

      //nahrat uzivatelovy levely
      fetch("process.php?action=get_levels", {method: 'get'})
          .then(response => response.json())
          .then(data => {
                this.game_levels = data.levels;
                this.game_levels.forEach(level => {
                  level.level_data = JSON.parse(level.level_data);
                })
                console.log(this.game_levels);

              }
          )



      this.$router.push({name: 'LevelManagement'});
    }
  },
  watch:{

  },
  computed: {

  }
}
</script>

<style scoped>
html, body {
  margin:0px;
  height:100%;
}

.login img{
  margin-left: 2px;
}

.login{
  width: fit-content;
  border: 1px solid black;
  border-radius: 5px;
}

.login-pos {
  margin-top: 30vh;
}

.app{
  margin-top: 0;
  height: 100vh;
}


.test {
  height: 50px;
  border: 1px solid black;
  background-color: gray;
}

.fit-width {
  width: fit-content;
}
