import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        isAuthenticated: false,
        current_user: {
            id: null,
            username: null,
            date_of_registration: null,
            role: null,
            icon: null,
            dark_mode: null,
            music: null,
        },

        game_levels: {},
        completed_game_levels: {},
        user_levels: {},
        completed_user_levels: {},
    },
    getters: {
        currentUser (state){
            return state.current_user;
        },
        currentUser_logged (state){
            return(state.current_user.username != null);
        },
        searchValue (state){
            return state.searchValue;
        },
        game_levels (state){
            return state.game_levels;
        },
        completed_game_levels (state){
            return state.completed_game_levels;
        },
        user_levels (state){
            return state.user_levels;
        },
        completed_user_levels (state){
            return state.completed_user_levels;
        },

    }
});