import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login'
import Missing from "@/views/Missing";
import LevelManagement from "@/views/LevelManagement";
import Profile from "@/views/Profile";
import UserLevelConfirmation from "@/views/UserLevelConfirmation";
import UserLevelManagement from "@/views/UserLevelManagement";
import UserManagement from "@/views/UserManagement";



import {store} from '@/store';
Vue.use(VueRouter)



const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    beforeEnter:(to, from, next) => {
      console.log(store.state.isAuthenticated);
      if (to.name === 'Login' && store.state.isAuthenticated) next({ name: 'Home' })
      else next()
    }
  },
  {
    path: '/LevelManagement',
    name: 'LevelManagement',
    component: LevelManagement
  },
  {
    path: '/UserLevelManagement',
    name: 'UserLevelManagement',
    component: UserLevelManagement
  },
  {
    path: '/UserLevelConfirmation',
    name: 'UserLevelConfirmation',
    component: UserLevelConfirmation
  },
  {
    path: '/UserManagement',
    name: 'UserManagement',
    component: UserManagement
  },
  {
    path: '/Profile',
    name: 'Profile',
    component: Profile
  },
  {
    path: '*',
    component: Missing
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  console.log(store.state.isAuthenticated);
  if (to.name !== 'Login' && !store.state.isAuthenticated) next({ name: 'Login' })
  else next()
})

export default router
