<template>
  <div class="home">
    <b-container>
      <b-row>
        <b-col>
          <b-form>
            <b-input v-model="name" type="text" placeholder="Jmeno"></b-input>
            <b-input v-model="surname" type="text" placeholder="Prijmeni"></b-input>
            <b-input v-model="username" type="text" placeholder="username"></b-input>
            <b-input v-model="role" type="number" placeholder="role"></b-input>

            <b-button @click="saveUser">Save User</b-button>
          </b-form>
        </b-col>
        <b-col>
          some testing value:
          {{current_user}}
        </b-col>
      </b-row>
    </b-container>

  </div>
</template>

<script>
// @ is an alias to /src
//1280x800
export default {
  name: 'Home',
  components: {
  },
  data() {
    return {

      username: null,
      name: null,
      surname: null,
      role: null,

    }
  },
  methods:{
    saveUser: function (){
      this.$store.state.current_user.username = this.username;
      this.$store.state.current_user.name = this.name;
      this.$store.state.current_user.surname = this.surname;
      this.$store.state.current_user.role = this.role;
    }
  },
  computed: {
    current_user(){
      return this.$store.getters.currentUser;
    }
  }
}
</script>
