<template>
  <nav :style="{ background: background}">
    <ul
        v-if="navLinks != null"
        :style="{ background: background}" ref="nav">
      <figure class="image-logo my-auto mx-1">
        <img :src="imagePath" height="40px" width="40px" />
      </figure>
      <li
          v-for="(link, index) in navLinks"
          :key="index"
          @mouseenter="$event.currentTarget.style.background = hoverBackground"
          @mouseleave="$event.currentTarget.style.background = background"
      >
        <router-link
            :to="link.path"
            :style="{color: linkColor}"
        >
            {{ link.text }}
        </router-link>
      </li>

      <li
          v-if="isAuthenticated"
          id="logout"
          class="ml-auto mr-1"
          @mouseenter="$event.currentTarget.style.background = hoverBackground"
          @mouseleave="$event.currentTarget.style.background = background"
          @click="logout"
      >
        <a href="">Logout</a>

      </li>
      <li
          v-else
          id="login"
          class="ml-auto mr-1"
          @mouseenter="$event.currentTarget.style.background = hoverBackground"
          @mouseleave="$event.currentTarget.style.background = background"
          @click="login"
      >
        <a href="">Login</a>

      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "Navbar",
  props:{
    //navLinks: {},
  },
  data(){
    return{
      //TODO zmenit na logo nonogramu
      imagePath: require('@/assets/logo.png'),
      background: '#333',
      hoverBackground: '#999',
      linkColor: '#DDD',

      user_navLinks: [
        {
          text: 'Přidat level',
          path: '/AddLevel',
        },
        {
          text: 'Profil',
          path: '/Profile',
        }
      ],

      admin_navLinks: [
        {
          text: 'Správa levelů',
          path: '/LevelManagement',
        },
        {
          text: 'Správa uživatelských levelů',
          path: '/UserLevelManagement',
        },
        {
          text: 'Potvrzování levelů',
          path: '/UserLevelConfirmation',
        },
        {
          text: 'Správa Uživatelů',
          path: '/UserManagement',
        },
        {
          text: 'Profil',
          path: '/Profile',
        }
      ],

    }
  },
  methods: {
    logout: function (){
      this.$store.state.isAuthenticated = false;
      this.$store.state.current_user = {
        id: null,
        username: null,
        date_of_registration: null,
        role: null,
        icon: null,
        dark_mode: null,
        music: null,
      }
      this.$router.push({name: 'Login'});
      this.$cookies.keys().forEach(cookie => {
        this.$cookies.remove(cookie);
      })
    },
    login: function (){
      this.$router.push({name: 'Login'});
    },
  },
  computed: {
    isAuthenticated(){
      return this.$store.state.isAuthenticated;
    },
    current_user(){
      return this.$store.getters.currentUser;
    },
    navLinks(){
      var links = new Array();
      if(this.current_user.role == 1){
        this.admin_navLinks.forEach(link =>{
          links.push(link);
        })

      }else if (this.current_user.role == 2){
        this.user_navLinks.forEach(link =>{
          links.push(link);
        })
      }
      return links;
    }
  }
}
</script>

<style scoped lang="scss">
nav {
  height: 60px;
  width: 100%;
  box-shadow: 2px 2px 2px #CCC;
  ul {
    display: flex;
    height: 100%;
    align-items: center;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
    box-shadow: 2px 2px 2px #CCC;

    figure {
      cursor: pointer;
      margin-right: 10px;
    }

    a {
      text-decoration: none;
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
    }

    i {
      margin-right: 10px;
      font-size: 22px;
    }

    li {
      list-style-type: none;
      padding: 10px 20px;
    }
  }
}
#logout{
  float: right;
  color: red;

  a{
    color: red;
  }
}


</style>